.header {
  display: flex;
  grid-area: h;
  background-color: #8179a3;
  justify-content: flex-start;
  height: 65px;
  }
  .login {
    margin-left: auto;
  }
  .login span {
    color: white;
    margin-right: 10px;
  }
  .login button {
    margin: 5px;
  }
  .userInfo {
  display: flex;
  width: 150px;
  height: auto;
  padding: 0px 10px 0px 10px;
  cursor: pointer;
  }

  .userInfo:hover {
    background-color: #7a7094;
  }

  .userLogin {
    padding-top: 5px;
  }
  .userAvatar img {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    margin: 5px;
  }
  .profileSettings {
    position: absolute;
    display: flex;
    flex-direction: column;
    display: none;
    background-color: rgba(180, 158, 240, 0.8);
    width: 170px;
  }
  